import React from 'react';

const Home = () => {
  return (
    <div className="screen-container">
      <p>
        Na CesteSNP.sk pracujeme dom a nocou, teda pokia nie sme akurt na horch, v prci, v
        kninici, v divadle, u frajerky, i sa hrme s naimi demi...
      </p>
      <p>
        Sekcia <a href="/pred/articles/1">Pred Cestou</a> je prv kde me vidie vsledky naej
        prce! Tto sekcia m hlavn cie zjednodui plnovanie tvojej Cesty hrdinov SNP. Pozri sa
        na to a daj nm vedie ako ti to pomohlo s plnovanm. Ak chce sledova n pokrok me
        sem samozrejme pravidelne chodi alebo sledova nae novinky na{' '}
        <a target="_blank" href="https://www.facebook.com/CestaSNPsk-185536644838/">
          Facebooku
        </a>.
      </p>
      <p>
        Taktie sa k nm me prida a pomoc s vvojom. Pouvame modern technolgie ako React,
        JavaScript ES6, Node.js a NoSQL databzu. To vetko be na Heroku cloude a tak m vek
        ancu sa naui osi nov. Ak to ale u vetko pozn, vbec nevhaj sa ozva a naopak
        naui ns ako postavi CestaSNP.sk na nohy rchlo a efektvne. Kontakt na ns:{' '}
        <a target="_blank" href="mailto:info@cestasnp.sk">
          info@cestasnp.sk
        </a>.
      </p>
      <p>
        CestuSNP.sk njde na{' '}
        <a target="_blank" href="https://github.com/hasty001/cestasnp-web">
          Githube
        </a>.
      </p>
    </div>
  );
};

export default Home;



